import classes from './dialogName1.module.css';
import DialogsMain from '../CreateMessage/dialogsMain';
import React from 'react';
import {Field, reduxForm, reset} from 'redux-form'
import {Dispatch} from "redux";
import {connect} from "react-redux";
import {addMessage1_ac as addMessage} from "../../../../../redusers/messages_reducer";
import {GlobalType} from "../../../../../redux-store";



const dialogName1Form  = function(props : any){
	return(
		<form onSubmit={props.handleSubmit}>
        <Field className={classes.area} placeholder="Write message..." maxLength="273" name='textarea'
        component='textarea' />
        <button className={classes.input}>Send</button>
		</form>

		)
}



const DialogName1FormContainer=reduxForm({
	form : 'dialogName1Form'
})(dialogName1Form)

type Props = {
	messages : Array<{message : string}>
	reset : Function
	addMessage : Function
}

const DialogName1 : React.FC<Props>=function(props){

	const windowREF=React.createRef();
	const addMessage=function(data : {textarea : string}){
		props.addMessage(data.textarea);
		props.reset()
	}
	const elements=props.messages.map((item,index) =><DialogsMain message={item.message} key={index} /> )

	return(
		// @ts-ignore
		<div className={classes.window} ref={windowREF}>
		<div className={classes.mainTest}>
		{elements}
		</div>
			{/*@ts-ignore*/}
		<DialogName1FormContainer onSubmit={addMessage}/>
		</div>
		)
}





const state_mtp=function(state : GlobalType){
	return{
		messages : state.messages.name1
	}
}
const resetForm = function (dispatch : Dispatch){
	return{
		reset : function (){
			dispatch(reset('dialogName1Form'))
		},
	}
}


export default connect(null,resetForm)(connect(state_mtp,{addMessage})(DialogName1))