import {messagesType} from "../types";

let initial: messagesType = {
    name1: [{message: 'Hello!'}],
    name2: [{message: 'Bye!'}],
    userNames: [{name: "Noname", adress: "1"},
        {name: "Friend", adress: "2"}]
}

const message_reducer = function (state = initial, action: addMessageAT): messagesType {
    switch (action.type) {
        case 'ADD-MESSAGE':
            if (!action.value) {
                return state;
            }
            if (action.index == 1) {
                return {...state, name1: [...state.name1, {message: action.value}]}
            }
            return {...state, name2: [...state.name2, {message: action.value}]}
        default :
            return state
    }
}

type addMessageAT = {
    type: 'ADD-MESSAGE',
    value: string,
    index: number
}

export let addMessage1_ac = function (value: string): addMessageAT {
    return {
        type: 'ADD-MESSAGE',
        value: value,
        index: 1

    }
}
export let addMessage2_ac = function (value: string): addMessageAT {
    return {
        type: 'ADD-MESSAGE',
        value: value,
        index: 2

    }
}
export default message_reducer;