import classes from './dialogsName.module.css';
import {NavLink} from 'react-router-dom';
import defaultImage from './../../../../source/img/svg/unknown.jpg'
import React from "react";

type Props = {
	name : string
	adress : string
}

const DialogsName : React.FC<Props>=function(props){
	// debugger
	return(
		<NavLink to={"/messages/"+props.adress} activeClassName={classes.active}>
		<div className={classes.main}>
			<img src={defaultImage} alt="" className={classes.image}/>
		<h3 className={classes.name}>{props.name}</h3>
		</div></NavLink>
		)
}


// name={item.name} adress={item.adress}
// key={index}
export default DialogsName