import classes from './messages.module.css';
import DialogsName from './dialogsName/dialogsName';
import {Redirect, Route} from 'react-router-dom';
import React from 'react';
import message_image from './../../../source/img/message.svg';
import {connect} from "react-redux";
import {GlobalType} from "../../../redux-store";
import DialogName1 from "./dialogsMain/DialogName1/dialogName1";
import DialogName2 from './dialogsMain/DialogName2/dialogName2'

type Props = {
    userAvatars : Array<{name : string, adress : "1" | "2" | "3"}>,
    logined : boolean | undefined
    processed : boolean

}


const Messages : React.FC<Props>= function (props) {
    if (props.logined == false && props.processed == true) {
        return <Redirect to={'/login'}/>
    }

    const elements = props.userAvatars.map((item, index) => <DialogsName name={item.name} adress={item.adress}
                                                                         key={index}
    />);
    return (
        <div className={classes.messages}>
            <div className={classes.flex}>
                <div className={classes.dialogs}>
                    <div className={classes.side}>
                        {elements}
                    </div>
                </div>
                <div className={classes.main}>
                    <img src={message_image} className={classes.messageImage} alt={''}/>
                    <h2 className={classes.selectUser}>Select user...</h2>
                    <Route component={DialogName1} path="/messages/1"/>
                    <Route component={DialogName2} path="/messages/2"/>
                    {/*<textarea className={classes.area} placeholder="White message..." ></textarea>*/}
                    {/*<button className={classes.input} onClick={addMessage}>Add Message</button>*/}
                </div>
            </div>
        </div>
    )
}


const state=function(state : GlobalType){
    return{
        userAvatars : state.messages.userNames,
        logined : state.app.logined,
        processed : state.app.initialized
    }
}
type stateType = {
    userAvatars : Array<{name : string, adress : "1" | "2" | "3"}>,
    logined : boolean | undefined
    processed : boolean
}


export default connect<stateType,{},{},GlobalType>(state,{})(Messages)