import classes from'./dialogsMain.module.css';
import defaultImage from './../../../../../source/img/svg/unknown.jpg'
import {connect} from "react-redux";
import {GlobalType} from "../../../../../redux-store";


type Props = {
	image : string | null,
	message : string
}

const DialogsMain : React.FC<Props>=function(props){
	return(
		<div className={classes.main}>
		<div className={classes.textBlock}>
		<p className={classes.message}>{props.message}</p></div>
		<div className={classes.imageBlock}>
		<img src={props.image ? props.image : defaultImage} alt="" className={classes.img} />
		</div>
		</div>
		)
}
let state = function (state : GlobalType){
	return{
		image : state.header.image
	}
}

type stateType = {
	image : string | null
}
type PropsType = {
	message : string
}

export default connect<stateType,{},PropsType,GlobalType>(state,{})(DialogsMain)